import React from "react";
import styled from "styled-components";
import Link from "gatsby-link";
import {Helmet} from "react-helmet";

import '../static/css/font-awesome.css'

const Container = styled.div`
  margin: 50px auto;
  max-width: 700px;
  padding: 1.25rem 1rem;
  box-sizing: border-box;
`;

const Header = styled.header`
  display: flex;
  justify-content: space-between;
  align-content: center;
  flex-wrap: wrap;
  margin-bottom: 1.5rem;
  box-sizing: border-box;

  @media (max-width: 700px) {
    flex-direction: column;
    align-content: flex-start;
  }
  border: dashed red;
`;

const TitleContainer = styled.div`
  flex: 70%;
  box-sizing: border-box;
  border: dashed red;
  display: flex;
  flex-direction: column;
`;

const TitleHeader = styled.h3`
  font-family: "Merriweather";
  font-size: 36pt;
  color: black;
  border: dashed red;
`;

const ContactBox = styled.div`
  flex: 30%;
  font-family: "Open Sans", sans;
  font-size: 9pt;
  box-sizing: border-box;

  @media (min-width: 700px) {
    padding: 25px 0;
  }
  border: dashed red;
`;

const MailPhone = styled.div`
  font-weight: bold;
`;

export default ({ children }) => (
  <Container>
    <Helmet>
      <title>Kaan Keskin</title>
      <meta charSet="utf-8"/>
      <meta name="description" content="Personal Website of Kaan Keskin"/>
      <meta name="keywords" content="kaan keskin, kaan, keskin, robotics, ai, machine learning, web developer"/>
      <meta name="robots" content="index,follow" />
    </Helmet>
    <Header>
      <TitleContainer>
        <TitleHeader>
            Kaan Keskin
        </TitleHeader>
      </TitleContainer>
      <ContactBox>
        Yayla Mah. Sinasi Dural Cad. <br/> Gursel Sok. No: 53/9 <br/>
        Tuzla/ISTANBUL <br/>
        Turkey
        <MailPhone>
          +90 542-475-3773 <br/>
          kaan@kaankeskin.me <br/>
          <i className="fa fa-twitter"></i>
          <i className="fa fa-facebook"></i>
        </MailPhone>
      </ContactBox>
    </Header>
    { children() }
  </Container>
)



